				"html": "<p>My first week of 2017 was rainy but the surf was great! Fitness wise, I was able to surf, ride my bike and get into the climbing gym a few times. As far as computer programming, I was very close to releasing a few open source products and started taking to the time to get up to date with basic knowledge of current popular MVCs. </p>\n<h2>Surfing has been great sans a little rain üåä</h2>\n<figure class=\"width--content\">\n  <img src=\"https://yowainwright.imgix.net/wk-1/countyline-surfing.jpg?fit=crop&w=700&h=700&auto=format\" alt=\"Countyline surf, January 7, 2017\" />\n</figure>\n<p>With a lot of rain in LA week, surfing was a safety hazard. This was because of runoff from land and all of the water being stirred up. What all of the rain has taken away from everyday surfing has been added to awesome waves on the few days I was able to get out. I went to <a href=\"http://www.surfline.com/surf-report/county-line-southern-california_4203/\">Countyline</a> with <a href=\"http://justinintime.com/\">Justin Berg</a>. The sets were consistant and the wave shapes were very nice. It was my first time in the water for 2017 so I was pleasantly was surprised I was able to turn on a few waves. </p>\n<figure>\n<iframe src=\"https://player.vimeo.com/video/199360358\" width=\"640\" height=\"360\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen style=\"display: block; margin-right: auto; margin-left: auto; max-height: 360px; height: 50vw; max-width: 100%;\"></iframe>\n<<figcaption>[Sunset surf spot](https://vimeo.com/199360358) on [Vimeo](https://vimeo.com)Vimeo.</figcaption>\n</figure>\n<p>On Sunday, I went to <a href=\"http://www.surfline.com/surf-report/sunset-beach-southern-california_119813/\">Sunset Surf Spot</a> and enjoyed some perfect waves. I got some long rides and even a few good turns. I was out on my <a href=\"http://www.benaipa.com/\">Aipa</a> Surfboard which doesn't seem to float as well anymore‚ÄîI still had so much fun.</p>\n<h2>Slow and steady makes the smile show üòä</h2>\n<p>Cycling was feeling pretty good and after adding <a href=\"https://touchstoneclimbing.com/cliffs-of-id/\">boulder climbing</a> to my workout routine‚Äîmy hips are feeling alright. I logged a little under <a href=\"https://www.strava.com/athletes/722335#interval?interval=201701&#x26;interval_type=week&#x26;chart_type=miles&#x26;year_offset=0\">150 miles</a>. My pace was slow and I still felt hesitant with hills. I was lucky to have <a href=\"https://twitter.com/D_fman\">Devin</a> join me on a few rides. </p>\n<figure>\n  <img src=\"http://yowainwright.imgix.net/wk-1/malaga-cove-cycling.jpg?fit=crop&w=2000&h=1500&auto=format\" alt=\"Cycling in Malaga Cove, January 8, 2017\" />\n</figure>\n<h2>Open Source, small things that grow üå±</h2>\n<p>Maintaining my first larger open source projects in <a href=\"https://github.com/dollarshaveclub/shave\">Shave</a> and <a href=\"https://github.com/dollarshaveclub/reframe.js\">Reframe.js</a> has continued to be interesting. I've learn from the questions I've received and the updates I've asked to make. Meanwhile, I've been working on <a href=\"https://github.com/dollarshaveclub/scrolldir/stargazers\">ScrollDir</a> with <a href=\"https://github.com/pwfisher\">Patrick Fisher</a> and another Project called StickyBits. </p>\n<h2>JavaScript Framework Learnings ü¶Ñ</h2>\n<p>I've had the opportunity to dig deeper into some JavaScript Frameworks. The frameworks that I've been learning more about are <a href=\"https://facebook.github.io/react/\">React</a>, <a href=\"https://vuejs.org/\">Vue</a> and <a href=\"https://svelte.technology/\">Svelte</a>. I've been comparing these frameworks to writing something that could be considered more basic using <a href=\"https://nodejs.org/en/\">Node</a> with <a href=\"http://expressjs.com/\">Express</a>. Knowing that these learnings will be done on a content site that still has relatively low traffic, speed on initial visit is very key. Hopefully, I can document more on my learnings soon! üê•</p>",
